@import './vars.scss';
@import './fonts.scss';

*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

html {
  font: 16px Montserrat, Helvetica, sans-serif;
  color: $color-primary-100;
}

body {
  max-width: 120rem;
  min-width: 20rem;
  margin: 0 auto;
}